<template>
  <div class="personnal-detail-wrap-note-container">
    <div class="personnal-detail-wrap-note-container-head">{{title}}</div>
    <div class="personnal-detail-wrap-note-container-box">
      <note-card
        class="fl"
        v-for="(item,i) in datas"
        :key="i"
        :item="item"
        @showdetail="showdetail"
        :class="{'mgr0':(i+1)%4==0}"
      />
      <div class="clear"></div>
    </div>
  </div>
</template>
<script>
import noteCard from "./noteCard.vue";
export default {
  components: {
    noteCard
  },
  props: {
    datas: Array,
    title: {
      type: String,
      default: ""
    }
  },
  methods:{
    showdetail(data) {
      this.$emit("showdetail", data);
    }
  }
};
</script>
<style lang="less" scoped>
.personnal-detail-wrap-note-container {
  width: 100%;
  margin-bottom: 32px;
  
  &-head {
    text-align: left;
    color: #b6b6b6;
    font-size: 0.9rem;
  }
  &-box {
    padding-top: 12px;
    display: flex;
    flex-wrap: wrap;
  }
}
</style>



