<template>
  <div class="note-card" @click="showdetail">
    <div class="note-card-icon fl">
      <i class="iconfont icon-biji"></i>
    </div>
    <div class="note-card-detail fr">
      <div class="note-card-detail-title">{{item.title}}</div>
      <div class="note-card-detail-title">{{item.coursename}}</div>
      <div class="note-card-detail-date">{{item.date}}</div>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    item: Object
  },
  methods:{
    showdetail() {
      this.$emit("showdetail", this.item);
    }
  } 
};
</script>
<style lang="less" scoped>
.fl {
  float: none;
}
.fr {
  float: none;
}
.note-card-detail{
  text-align: left;
  margin-left: 10px;
}
.note-card {
  display: flex;
  justify-content: center;
  width: 210px;
  border: 1px solid #ebebeb;
  border-radius: 8px;
  padding: 10px;
  margin-bottom: 20px;
  margin-right: 10px;
  margin-left: 10px;
  padding-left: 10px;
  &-icon {
    color: #31a1fc;
    height: 43px;
    line-height: 75px;
    .iconfont {
      font-size: 2.5rem;
    }
  }
  &-detail {
    &-date {
      font-size: 0.8rem;
      color: #898989;
      text-align: left;
    }  
    &-title{
      overflow: hidden;
      text-overflow:ellipsis;
      white-space: nowrap; 
      width: 140px;
    }
  }
  &:hover {
    border-color: #31a1fc;
  }
}
</style>



