<template>
  <div class="personnal-detail-wrap">
    <div class="personnal-detail-wrap-head">
      <span class="personnal-detail-wrap-head-title">笔记管理</span> 
      <div> 
          <el-date-picker
            v-model="dateRange"
            size="small"
            type="daterange"
            align="right"
            unlink-panels
            format="yyyy-MM-dd"
            value-format="yyyy-MM-dd"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            @change="selectDateRange"
            style="width: 360px;"
          ></el-date-picker>
          <el-input placeholder="请输入内容" size="small"  v-model="inputSearch" style="width:250px;">
            <el-button type="primary" slot="append" @click="searchData">搜索</el-button>
          </el-input>  
        </div>
         <span class="personnal-detail-wrap-head-add" @click="addNote">
        <i class="iconfont icon-tianjiahuabu"></i>
        添加
      </span>
    </div>  
      <note-container title="" :datas="weekNotes" @showdetail="showdetail"/> 
      <div v-if="nodata">未找到数据</div>
    <div class="personal-bottom-pager">
      <el-pagination
        class="personnal-detail-wrap-pager"
        layout="prev, pager, next"
        :current-page.sync="formData.pno"
        :page-size="formData.psize"
        :total="dataTotal"
        prev-text="上一页"
        next-text="下一页"
        @current-change="pageChange"/>
    </div>
    <el-dialog
      title="添加/修改笔记"
      class="student-add-note-dialog"
      :close-on-click-modal="false"
      :visible.sync="isFormShow"
      :before-close="resetForm">
      <el-form :model="form" :rules="rules" ref="form" size="small" label-width="90px">
        <el-form-item label="笔记名称：" prop="Name" placement="right">
          <el-input v-model="form.Name" clearable></el-input>
        </el-form-item>
        <el-form-item label="课程名称：" prop="Lesson" placement="right"> 
          <el-select
            v-model="form.Lesson" 
            filterable
            remote
            collapse-tags
            reserve-keyword
            placeholder="请输入关键词"
            :remote-method="remoteMethodOther">
            <el-option
              v-for="item in CourseOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="笔记内容：" prop="Desc" placement="right"> 
          <summer-note :filetype="5"></summer-note>
        </el-form-item>
      </el-form>
      <div slot="footer">
        <el-button :loading="addLoading" type="primary" size="small" @click="comfirmFormHandler">
          <i class="el-icon-check el-icon--left"></i>确 定
        </el-button>
        <el-button @click="resetForm" size="small">
          <i class="el-icon-close el-icon--left"></i>取 消
        </el-button>
      </div>
    </el-dialog> 
  </div>
</template>
<script>
import noteContainer from "../../../components/personal/student/noteContainer.vue";
import summerNote from "../../../components/personal/summernote.vue";
import { formatDate } from "@/utils/utils";  
import api from "../../../utils/api";

export default {
  components: {
    noteContainer,
    summerNote
  },
  data() {
    return {
      todayNotes: [],
      dataTotal: 0,
      snid: "",
      nodata: true,
      dateRange: [],
      CourseOptions: [],
      inputSearch: "",
      formData: {
        startDate: "",
        endDate: "",
        psize: 20,
        pno: 1
      },
      weekNotes: [],
      isFormShow: false,
      isDetailFormShow: false,
      addLoading: false,
      form: {
        Name: "",
        Lesson: "",
        Subject: "",
        Desc: ""
      },
      rules: {},
      fileList: [],
      subjectOptions: [],
      SnName: "",
      snCourse: ""
    };
  },
  mounted() {
    this.dateRange = [];
    this.dateRange[0] = new Date();
    this.dateRange[1] = new Date();
    this.formData.startDate = formatDate(new Date(), "yyyy-MM-dd");
    this.formData.endDate = formatDate(new Date(), "yyyy-MM-dd");
    this.searchData();
    this.loadAllCourse();
  },
  methods: {
    pageChange(page) {
      this.formData.pno = page;
      this.searchData();
    },
    addNote() {
      //   获取表单所需数据
      //   成功获取后
      this.isFormShow = true;
    },
    resetForm() {
      this.$set(this.form, "Name", "");
      this.$set(this.form, "Lesson", "");
      this.$set(this.form, "Subject", "");
      this.$set(this.form, "Desc", "");
      this.isFormShow = false;
      this.isDetailFormShow = false;
      this.snid = "";
      this.SnName = "";
      this.snCourse = "";
      $(".summernote").summernote("code", "");
    },
    comfirmFormHandler() {
      if (this.form.Name == "") {
        this.$message({
          dangerouslyUseHTMLString: true,
          showClose: true,
          message: "请填写笔记名称",
          type: "warning"
        });
        return;
      }
      if (this.form.Lesson == "") {
        this.$message({
          dangerouslyUseHTMLString: true,
          showClose: true,
          message: "请选择课程",
          type: "warning"
        });
        return;
      }
      this.addLoading = true;
      /* 发送请求
        *********
        请求成功*/
      this.addLoading = false;
      this.isFormShow = false;

      var htmstr = $(".summernote").summernote("code");
      var newContent = htmstr.replace(
        /<img [^>]*src=['"]([^'"]+)[^>]*>/gi,
        function(match, capture) { 
          var newStr = '<img src="picture/' + capture.split("=")[1].split("&")[0] + '/5" alt="" />';
          return newStr;
        }
      ); 
      this.$http
        .post(this.$api.SaveStuNode, {
          snID: this.snid,
          title: this.form.Name,
          content: newContent,
          courseID: this.form.Lesson,
          studentID: this.$http.getStu().StudentID
        })
        .then(res => {
          if (res.Result) {
            this.$message({
              dangerouslyUseHTMLString: true,
              showClose: true,
              message: "数据保存成功！",
              type: "success"
            });
            this.searchData();
          }else {
            this.$message({
              dangerouslyUseHTMLString: true,
              showClose: true,
              message: res.Info,
              type: "warning"
            });
          }
        })
        .catch(err => {});
    },
    selectDateRange() {
      if (this.dateRange.length === 2) {
        this.formData.startDate = this.dateRange[0];
        this.formData.endDate = this.dateRange[1];
      }
    },
    searchData() {
      this.weekNotes = [];
      this.$http
        .get(this.$api.GetStudentNoteDatas, {
          startDate: this.formData.startDate,
          endDate: this.formData.endDate,
          psize:this.formData.psize,
          pno:this.formData.pno,
          snid: "",
          stuid: this.$http.getStu().StudentID,
          estate: 1,
          key: this.inputSearch
        })
        .then(res => {
          if (res.Result && res.Datas && res.Datas.length > 0) {
            this.nodata = false;
            res.Datas.forEach(item => {
              this.weekNotes.push({
                snid: item.Key.SNID,
                title: item.Key.SNTtile,
                coursename: item.Value.CourseName,
                date: formatDate(item.Key.CreateTime, "yyyy-MM-dd hh:mm:ss")
              });
            });
          }
          if (res.Result) {
            this.dataTotal = res.RowsCount || 0;
          } else {
            this.dataTotal = 0;
          }
        })
        .catch(err => {});
    },

    //加载学生课程
    async loadAllCourse() {
      this.tempCourse = [];
      this.$http
        .get(this.$api.GetDatasByStudentID, { studentID: this.$http.getStu().StudentID })
        .then(res => {
          if (res.Result && res.Datas && res.Datas.length > 0) {
            res.Datas.forEach(element => {
              this.tempCourse.push({
                label: element.CourseName + "(上课时间：" + formatDate(element.ClassDate, "yyyy-MM-dd") + ")",
                value: element.CourseID
              });
            });
          }
          this.CourseOptions = this.tempCourse;
        })
        .catch(err => {});
    },
    remoteMethodOther(query) {
      setTimeout(() => {
        this.CourseOptions = this.tempCourse.filter(item => {
          return item.label.toLowerCase().indexOf(query.toLowerCase()) > -1;
        });
      }, 200);
    },
    async showdetail(data) {
      this.snid = data.snid;
      let downloadURL = await api.GetI_DownloadFileURL();
      this.$http
        .get(this.$api.GetStudentNoteDatas, {
          startDate: this.formData.startDate,
          endDate: this.formData.endDate,
          snid: data.snid,
          stuid: this.$http.getStu().StudentID,
          estate: 1,
          key: this.inputSearch
        })
        .then(res => {
          if (res.Result && res.Datas && res.Datas.length > 0) {
            this.isFormShow = true;
            res.Datas.forEach(item => {
              this.form.Name = item.Key.SNTtile;
              this.form.Lesson = item.Key.CourseID;
              var htmstr = item.Key.SNContent;  
              var newContent = htmstr.replace(
                /<img [^>]*src=['"]([^'"]+)[^>]*>/gi,
                function(match, capture) {  
                  var filename = capture.split('/')[1]; 
                  var newStr = '<img src="' + downloadURL + '?filename=' + filename + '&fileType=5" alt="" />';
                  return newStr;
                }
              ); 
              setTimeout(() => {
                $(".summernote").summernote("code", newContent);
              }, 500);
            });
          }
          if (res.Result) {
            this.dataTotal = res.RowsCount || 0;
          }
        })
        .catch(err => {});
    }
  }
};
</script>
<style lang="less">
.personnal-detail-wrap {
  &-head {
    width: 100%;
    height: 52px;
    border-bottom: 1px solid #eaedf2;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    margin-bottom: 15px;
    &-add {
      color: #2a9efc;
      padding: 2px 10px 2px 10px;
      border: 1px solid #2a9efc;
      border-radius: 5px;
      user-select: none;
      cursor: pointer;
      .iconfont {
        vertical-align: middle;
      }
    }

    &-title {
      margin-top: 5px;
      font-size: 14px;
    }
  }
}
.student-add-note-dialog {
  .el-dialog {
    width: 52%;
    .el-dialog__body {
      text-align: left;
    }
  }
}
.personal-bottom-pager {
  text-align: right;
  user-select: none;
  margin-top: 25px;

  .btn-next,
  .btn-prev,
  .el-pager li {
    border: 1px solid #dfdfdf;
    color: #666;
    font-weight: 400;
  }

  .el-pager li {
    border-left: none;
  }

  .el-pager li.active {
    background: #409eff;
    color: #fff;
    border-color: #409eff;
  }

  .btn-next,
  .btn-prev {
    padding: 0 5px;
  }

  .btn-prev {
    border-top-left-radius: 3px;
    border-bottom-left-radius: 3px;
  }

  .btn-next {
    border-top-right-radius: 3px;
    border-bottom-right-radius: 3px;
    border-left: none;
  }
}
</style>



